@import '../../global.modules.css';

.container{
    position: relative;
    box-sizing: border-box;
    top: 13vh;
    left: 2vw;
    width: 40%;
    padding: 8px 8px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    justify-content: space-evenly;
    color: white;
    background-color: var(--dark-black-shadow);
    border-radius: 15px;
    font-size: var(--mobile-p-font-size);
}
.label{
    margin-right: 10px;
}
.button{
    text-decoration: none;
    display: inline-block;
    height: 30px;
    max-height: auto;
    font-size: .9rem;
    width: 90%;
    color: white;
    background-color: rgba(0,0,0,.0);
    padding: 4px;
    border-radius: 5px;
    border: none;
    font-size: var(--mobile-p-font-size);
    transition: 150ms ease;
}
.button:hover{
    border: 1px solid rgba(255,255,255,.2);
    background-color: rgba(255,255,255,.1);
    cursor: pointer;
}
.button:active{ 
    background-color: rgb(123, 12, 12);
}
.input{
    width: 90%;
    max-width: 150px;
}
.select{
    background-color: rgba(0,0,0,.5);
    color: white;
    width: 90%;
    height: 30px;
    margin-bottom: 10px;
    text-align: center;
    padding: 0px;
}
.genreButton{
    box-sizing: border-box;
    text-decoration: none;
    display: inline-block;
    height: 60px;
    font-size: .9rem;
    width: 90%;
    color: white;
    background-color: rgba(0,0,0,.0);
    padding: 4px;
    border-radius: 5px;
    border: none;
    transition: 150ms ease;
}
.genreButton:hover{
    box-sizing: border-box;
    border: 1px solid rgba(255,255,255,.2);
    background-color: rgba(255,255,255,.1);
    cursor: pointer;
}
.genreButton:active{
    background-color: rgb(123, 12, 12);
}
@media screen and( min-width: 800px){
    .container{
        position: relative;
        box-sizing: border-box;
        padding: 8px 8px;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        justify-content: space-evenly;
        width: 16%;
        height: 83vh;
        top: 13vh;
        left: 2vw;
        color: white;
        background-color: var(--dark-black-shadow);
        border-radius: 15px;
    }
}